/**
 * @Author JohnLi
 * @Date 2018/4/19 17:46
 */
import AppMain from '@/pages/app-main/AppMain';
import Interface from '@/pages/interface/Interface';
import Database from '@/pages/database/Database';
import Auth from '@/pages/auth/Auth';
import Apply from '@/pages/auth/Apply';
import MyApplication from '@/pages/auth/MyApplication';
import Check from '@/pages/auth/Check';
import Settings from '@/pages/auth/Settings';
import BusinessList from '@/pages/business-manage/BusinessList';
import Plugins from '@/pages/plugins/Plugins';
import NotFound from '@/components/app-exception/NotFound.vue';

import store from '../store/index';

/*
*  Vue-router   https://router.vuejs.org/zh-cn/api/options.html#routes
*icon
*label
*hidden 
* childrenPosition: 
* permission: ID
* */

const mainRoutes = [
  {
    path: '/interface',
    name: 'Interface',
    icon: 'fa-link',
    label: '',
    component: Interface,
    permission: [2, 3, 99], // 
    // 
    beforeEnter: (to, from, next) => {
      const userMsg = store.state.userMsg;
      const permission = [2, 3, 99];
      permission.includes(userMsg.role_id) ? next() : next({
        path: '/auth' // 
      });
    }
  },
  {
    path: '/database',
    name: 'Database',
    icon: 'fa-database',
    label: '',
    permission: [], // 
    component: Database
  },
  {
    path: '/business',
    name: 'Business',
    icon: 'fa-briefcase',
    label: '',
    component: BusinessList,
    permission: [99],
    // 
    beforeEnter: (to, from, next) => {
      const userMsg = store.state.userMsg;
      const permission = [99];
      permission.includes(userMsg.role_id) ? next() : next('/');
    }
  },
  {
    path: '/settings',
    name: 'Settings',
    icon: 'fa-users',
    label: '',
    component: Settings,
    permission: [99],
    // 
    beforeEnter: (to, from, next) => {
      const userMsg = store.state.userMsg;
      const permission = [99];
      permission.includes(userMsg.role_id) ? next() : next({
        path: '/auth'
      });
    }
  },
  {
    path: '/auth',
    icon: 'fa-th-large',
    label: '',
    component: Auth,
    childrenPosition: 'top',
    children: [
      {
        path: 'apply',
        name: 'Apply',
        label: '',
        component: Apply
      },
      {
        path: 'myApplication',
        name: 'myApplication',
        label: '',
        component: MyApplication
      },
      {
        path: 'check',
        name: 'Check',
        label: '',
        component: Check,
        permission: [2, 3, 99],
        // 
        beforeEnter: (to, from, next) => {
          const userMsg = store.state.userMsg;
          const permission = [2, 3, 99];
          permission.includes(userMsg.role_id) ? next() : next({
            path: '/auth'
          });
        }
      },
      {
        path: '',
        redirect: 'apply',
        hidden: true
      }
    ]
  },
  {
    path: '/plugins',
    name: 'Plugins',
    label: '',
    component: Plugins,
    hidden: true //   
  },
  {
    path: '',
    redirect: '/auth',
    hidden: true
  }
];

const routes = [
  {
    path: '/',
    component: AppMain,
    children: mainRoutes
  },
  {
    path: '**',
    component: NotFound
  }
];

export { routes, mainRoutes };



