<template>
  <el-dialog
      custom-class="business-details-dialog"
      title=""
      :close-on-click-modal="false"
      :before-close="close"
      :visible.sync="dialogVisible"
    >
      <div class="dialog-body">
        <el-form ref="business-details-form" :model="copyData" label-width="150px"  size="mini" :rules="rules">
            <el-form-item label="" prop="bname">
              <el-input v-model="copyData.bname" placeholder="" size="small"></el-input>
            </el-form-item>
            <el-form-item label="kafkaip" prop="bkafka_ip">
              <el-input v-model="copyData.bkafka_ip" placeholder="kafkaip" size="small"></el-input>
            </el-form-item>
            <el-form-item label="kafka" prop="bkafka_port">
              <el-input v-model="copyData.bkafka_port" placeholder="kafka" size="small"></el-input>
            </el-form-item>
            <el-form-item label="kafka" prop="bkafka_user">
              <el-input v-model="copyData.bkafka_user" placeholder="kafka" size="small"></el-input>
            </el-form-item>
            <el-form-item label="kafka" prop="bkafka_pkey">
              <el-input v-model="copyData.bkafka_pkey" placeholder="kafka" size="small" type="textarea" :autosize="{ minRows: 1, maxRows: 4}"></el-input>
            </el-form-item>
            <el-form-item label="hiveip" prop="bhive_ip">
              <el-input v-model="copyData.bhive_ip" placeholder="hiveip" size="small"></el-input>
            </el-form-item>
            <el-form-item label="hive" prop="bhive_port">
              <el-input v-model="copyData.bhive_port" placeholder="hive" size="small"></el-input>
            </el-form-item>
            <!--<el-form-item label="hive" prop="bhive_user">-->
              <!--<el-input v-model="copyData.bhive_user" placeholder="hive" size="small"></el-input>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="hive" prop="bhive_password">-->
              <!--<el-input v-model="copyData.bhive_password" placeholder="hive" size="small"></el-input>-->
            <!--</el-form-item>-->
            <el-form-item label="impalaip" prop="bimpala_ip">
              <el-input v-model="copyData.bimpala_ip" placeholder="impalaip" size="small"></el-input>
            </el-form-item>
            <!--<el-form-item label="impala" prop="bimpala_port">-->
              <!--<el-input v-model="copyData.bimpala_port" placeholder="impala" size="small"></el-input>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="impala" prop="bimpala_user">-->
              <!--<el-input v-model="copyData.bimpala_user" placeholder="impala" size="small"></el-input>-->
            <!--</el-form-item>-->
            <el-form-item label="zookeeper" prop="bzk_host">
              <el-input v-model="copyData.bzk_host" placeholder="zookeeper" size="small"></el-input>
            </el-form-item>
            <el-form-item label="zookeeper" prop="bzk_path">
              <el-input v-model="copyData.bzk_path" placeholder="zookeeper" size="small"></el-input>
            </el-form-item>
            <el-form-item label="hiveflume" prop="bflume_endpoint">
              <el-input v-model="copyData.bflume_endpoint" placeholder="hiveflume" size="small"></el-input>
            </el-form-item>
            <el-form-item label="kudokafaka connect" prop="bkafka_connector_endpoint">
              <el-input v-model="copyData.bkafka_connector_endpoint" placeholder="kudokafaka connect" size="small"></el-input>
            </el-form-item>
            <el-form-item label="kudo master host" prop="bkudu_master_host">
              <el-input v-model="copyData.bkudu_master_host" placeholder="kudo master host" size="small"></el-input>
            </el-form-item>
            <el-form-item label="" prop="bmanager_workids">
              <div id="etl-person-selector"></div>
            </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close()"> </el-button>
        <el-button type="primary" @click="save()"> </el-button>
      </span>
    </el-dialog>
</template>

<script>
import { isInteger, isIp } from 'utils';
export default {
  name: 'BusinessDetailsDialog',
  data () {
    const _isInteger = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      } else if (!isInteger(value)) {
        return callback(new Error(''));
      } else {
        callback();
      }
    };

    const _isIp = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      } else if (!isIp(value)) {
        return callback(new Error('ip'));
      } else {
        callback();
      }
    };
    return {
      copyData: {},
      dialogVisible: false,
      rules: {
        bname: [
          { required: true, message: '', trigger: 'blur' }
        ],
        bename: [
          { required: true, message: '', trigger: 'blur' }
        ],
        bkafka_ip: [
          { required: true, message: 'kafkaip', trigger: 'blur' }
        ],
        bkafka_port: [
          { trigger: 'blur', validator: _isInteger }
        ],
        bkafka_user: [
          { required: true, message: 'kafka', trigger: 'blur' }
        ],
        bkafka_pkey: [
          { required: true, message: 'kafka', trigger: 'blur' }
        ],
        bhive_ip: [
          { trigger: 'blur', validator: _isIp }
        ],
        bhive_port: [
          { trigger: 'blur', validator: _isInteger }
        ],
        bhive_user: [
          { required: true, message: 'hive', trigger: 'blur' }
        ],
        bhive_password: [
          { required: true, message: 'hive', trigger: 'blur' }
        ],
        bimpala_ip: [
          { trigger: 'blur', validator: _isIp }
        ],
        bimpala_port: [
          { trigger: 'blur', validator: _isInteger }
        ],
        bimpala_user: [
          { required: true, message: 'impala', trigger: 'blur' }
        ],
        bzk_host: [
          { required: true, message: 'zookeeper', trigger: 'blur' }
        ],
        bzk_path: [
          { required: true, message: 'zookeeper', trigger: 'blur' }
        ],
        bflume_endpoint: [
          { required: true, message: 'hiveflume', trigger: 'blur' }
        ],
        bkafka_connector_endpoint: [
          { required: true, message: 'kudokafaka connect', trigger: 'blur' }
        ],
        bkudu_master_host: [
          { required: true, message: 'kudo master host', trigger: 'blur' }
        ],
        bmanager_workids: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  props: ['data', 'mode'],
  components: {
  },
  mounted () {
  },
  methods: {
    initSelector (defaultSelect = []) {
      /* eslint-disable no-undef */
      $('#etl-person-selector').uCheck('etl-person-selector', defaultSelect, {
        size: 8,
        callback: (selectList) => {
          this.copyData['bmanager'] = selectList.join(',');
          this.copyData['bmanager_info'] = selectList.join(',');
          this.copyData['bmanager_workids'] = selectList.join(',');
        }
      });
    },
    open () {
      this.dialogVisible = true;
    },
    close () {
      this.dialogVisible = false;
      this.resetForm('business-details-form');
    },
    paramsValidate () {},
    sendRequest () {
      const url = this.mode === 'add' ? 'business/add/' : 'business/update/';
      if (this.mode === 'edit') {
        this.copyData.bid = this.copyData.id;
        delete this.copyData.id;
      }
      this.$http.post(url, this.copyData).then(res => {
        this.close();
        this.$emit('confirm');
      });
    },
    save () {
      this.$refs['business-details-form'].validate((valid) => {
        if (valid) {
          this.sendRequest();
        } else {
          return false;
        }
      });
    },
    resetForm (formName) {
      this.$refs[formName].resetFields();
    }
  },
  watch: {
    dialogVisible (visible) {
      if (visible) {
        this.copyData = JSON.parse(JSON.stringify(this.data));
        this.$nextTick(() => {
          const defaultSelected = this.copyData['bmanager_workids'] ? this.copyData['bmanager_workids'].split(',') : [];
          this.initSelector(defaultSelected);
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
  /deep/ .business-details-dialog {
    min-width: 600px;
    width: 60%;
    .el-dialog__body {
      max-height: 400px;
      overflow: auto;
    }
    .dialog-body {
      min-width: 800px;
      .el-form {
        display: flex;
        flex-wrap: wrap;
        .el-form-item {
          flex: 48%;
        }
      }
    }
  }
</style>



