<template>
  <div class="business-manage">
    <AppTable
      title=""
      :loading="loading"
      :tableRows="20"
      :border="true"
      :tableColumns="tableColumns"
      :tableData="tableData"
    >
      <template slot="custom-toolbar">
        <el-button type="primary" size="mini" @click="addBusiness()" class="custom-toolbar-wrap"></el-button>
      </template>
      <template slot="operate" slot-scope="scope">
        <el-button type="primary" @click="openDetailsDialog(scope.row)" class="etl-btn mini" icon="el-icon-info" title=""></el-button>
        <el-button type="danger" @click="delBusiness(scope.row)" class="etl-btn mini" icon="el-icon-delete" title=""></el-button>
      </template>
    </AppTable>

    <BusinessDetailsDialog :data="currentRow" ref="detailsDialog" :mode="mode" @confirm="confirm()"></BusinessDetailsDialog>
  </div>
</template>

<script>
import BusinessDetailsDialog from './BusinessDetailsDialog';
export default {
  name: 'business-list',
  data () {
    return {
      tableColumns: [
        { label: '', field: 'bname' },
        { label: 'kafkaip', field: 'bkafka_ip' },
        { label: 'kafka', field: 'bkafka_port' },
        { label: 'kafka', field: 'bkafka_user' },
        { label: 'kafka', field: 'bkafka_password' },
        { label: 'kafka  topic', field: 'bkafka_topic' },
        { label: 'hiveip', field: 'bhive_ip' },
        { label: 'hive', field: 'bhive_port' },
        { label: 'hive', field: 'bhive_user' },
        { label: 'hive', field: 'bhive_password' },
        { label: 'hive', field: 'bhive_db_name' },
        { label: 'impalaip', field: 'bimpala_ip' },
        { label: 'impala', field: 'bimpala_port' },
        { label: 'impala ', field: 'bimpala_user' },
        { label: 'impala ', field: 'bimpala_password' },
        { label: 'impala ', field: 'bimpala_db_name' },
        { label: 'zookeeper', field: 'bzk_host' },
        { label: 'zookeeper', field: 'bzk_path' },
        { label: 'hiveflume', field: 'bflume_endpoint' },
        { label: 'kudokafaka connect', field: 'bkudo_endpoint' },
        { label: '', field: 'bmanager_info' },
        { label: '', field: 'bstatus' },
        { label: '', field: 'operate', width: 100, fixed: 'right' }
      ],
      tableData: [],
      currentRow: {},
      mode: 'add',
      loading: false
    };
  },
  props: [],
  components: {
    BusinessDetailsDialog
  },
  mounted () {
    this.getBusinessList();
  },
  methods: {
    getBusinessList () {
      this.loading = true;
      this.$http.get('business/admin/list/').then(data => {
        this.tableData = data.data;
        this.loading = false;
      });
    },
    openDetailsDialog (row) {
      this.currentRow = row;
      this.mode = 'edit';
      this.$refs.detailsDialog.open();
    },
    addBusiness () {
      this.currentRow = {};
      this.mode = 'add';
      this.$refs.detailsDialog.open();
    },
    del (params) {
      return this.$http.post('business/delete/', params);
    },
    delBusiness (row) {
      this.currentRow = row;
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.del({bid: row.id}).then(res => {
          this.getBusinessList();
        });
      }).catch(() => {});
    },
    confirm () {
      this.getBusinessList();
    }
  }
};
</script>

<style lang="scss" scoped>

</style>



