<template>
  <div class="app-plugins">
    <AppOptions :options="options" @optionsSelectedChange="change($event)" @optionsReady="optionsReady()"></AppOptions>
      <AppMenu :menus="routesConfig"></AppMenu>
  </div>
</template>

<script>
import { mainRoutes } from '@/router/routes';

export default {
  data () {
    return {
      options: [
        {
          type: 'dropdown',
          labelName: 'bname',
          valueName: 'id',
          query: {
            url: 'business/list/',
            params: {type: 'my'}
          },
          sync: true
        },
        {
          type: 'multiSelect',
          defaultSelectedNumber: 1,
          labelName: 'bname',
          valueName: 'id',
          query: {
            url: 'business/list/',
            params: {type: 'my'}
          },
          sync: true
        },
        {
          type: 'singleSelect',
          horizontalCount: 2,
          list: [
            {label: '', value: 0},
            {label: '', value: 1},
            {label: '', value: 2}
          ]
        },
        {
          type: 'cascade',
          selected: [1],
          list: [{
            value: 'zhinan',
            label: '',
            children: [{
              value: 'shejiyuanze',
              label: '',
              children: [{
                value: 'yizhi',
                label: ''
              }, {
                value: 'fankui',
                label: ''
              }, {
                value: 'xiaolv',
                label: ''
              }, {
                value: 'kekong',
                label: ''
              }]
            }, {
              value: 'daohang',
              label: '',
              children: [{
                value: 'cexiangdaohang',
                label: ''
              }, {
                value: 'dingbudaohang',
                label: ''
              }]
            }]
          }]
        }
      ],
      routesConfig: mainRoutes
    };
  },
  mounted () {
  },
  components: {
  },
  methods: {
    change (data) {
    },
    optionsReady () {
    }
  }
};
</script>



