// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

import store from './store';

import upperFirst from 'lodash/upperFirst';
import camelCase from 'lodash/camelCase';

import { isNullObject } from 'utils';

// Element-UI  
// import ElementUI from 'element-ui';

// 
import { Button, Select, Option, Table, TableColumn, Input, RadioButton, CheckboxButton, CheckboxGroup, Checkbox, Notification, MessageBox,
  RadioGroup, Tabs, Popover, Menu, MenuItem, Submenu, Card, Dialog, Pagination, Loading, Cascader, Form, FormItem, Switch, Col, Row } from 'element-ui';

import router from './router';

import ETLPlugin from './plugins/ETLCommonPlugin';

// element-ui 
// import 'element-ui/lib/theme-chalk/index.css';
// element-ui 
import '../theme/index.css';
// css
import './assets/styles/initialize.css';
// font-awesome
import 'font-awesome/css/font-awesome.css';

Vue.config.productionTip = false;
// Vue.use(ElementUI);

Vue.use(Button);
Vue.use(Select);
Vue.use(Option);
Vue.use(Input);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Tabs);
Vue.use(Popover);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Submenu);
Vue.use(Card);
Vue.use(RadioButton);
Vue.use(Checkbox);
Vue.use(CheckboxButton);
Vue.use(CheckboxGroup);
Vue.use(RadioGroup);
Vue.use(Dialog);
Vue.use(Pagination);
Vue.use(Cascader);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Switch);
Vue.use(Col);
Vue.use(Row);

Vue.use(Loading.directive);

Vue.prototype.$loading = Loading.service;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;

Vue.prototype.$notice = Notification;

Vue.use(ETLPlugin);

// 
// 
function getUserMsg () {
  return new Promise((resolve, reject) => {
    Vue.http.get('user/info/').then(res => {
      resolve(res.data);
    }).catch(error => {
      reject(error);
    });
  });
}
// 
router.beforeEach((to, from, next) => {
  if (!isNullObject(store.state.userMsg)) {
    getUserMsg().then(user => {
      store.dispatch('setUserMsg', user);
      next();
    }).catch(error => {
      next(error);
    });
  } else {
    next();
  }
});

// 
const requireComponent = require.context(
  // 
  './components/',
  // 
  true,
  // 
  /[A-Z]\w+\.(vue|js)$/
);
requireComponent.keys().forEach(fileName => {
  // 
  const componentConfig = requireComponent(fileName);
  const regexp = /.+\/(.+)\./;
  //  `'./` 
  fileName = regexp.exec(fileName)[1];
  //  PascalCase 
  const componentName = upperFirst(
    camelCase(
      fileName
    )
  );
  // 
  Vue.component(
    componentName,
    //  `export default` 
    //  `.default`
    // 
    componentConfig.default || componentConfig
  );
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
});



